.main {
    width: 100%;
    height: 100vh;
}

video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
}

.button {
    background-color: #333;
    color: #fff;
    font-weight: bolder;
    padding: 10px 15px;
    border: none;
    border-radius: 0.5em;
}

.button:hover {
    background: greenyellow;
    color: black;
    font-weight: bolder;
}  